body {
  display: flex;
  align-items: center;
  justify-content: center;
  min-height: 100vh;
  background-color:#f5e7e7;
}

.wrapper, 
.wrapper .profile-image,
.buttons button{
  background-color: #ecf0f3;
  border-radius: 10px;
  box-shadow: -3px -3px 7px #ffffff,
                3px 3px 5px #ceced1;
}

.wrapper {
  background-color: #ecf0f3;
  padding: 30px;
  width: 250px;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}

.wrapper .profile-image {
  height: 150px;
  width: 150px;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.profile-image .inner-area {
  height: calc(100% - 25px);
  width: calc(100% - 25px);
}

.inner-area img {
  height: 100%;
  width: 100%;
  border-radius: 50%;
  object-fit: cover;
}

.wrapper .buttons{
  display: flex;
  width: 100%;
  justify-content: space-between;
}

.buttons button {
  width: 100%;
  border: none;
  outline: none;
  padding: 12px 0;
  border-radius: 5px;
  cursor: pointer;
}

.buttons button:hover {
  background-color: #f5e7e7;
}
.buttons button:first-child {
  margin-right: 10px;
}
.buttons button:last-child{
  margin-left: 10px;
}

ul {
  list-style-type: none;
  display: flex;
  justify-content: space-between; 
  margin: 15px;
  padding: 0; 
}
li {
  margin: 10px;
}

.github {
  color: #333;
}
.linked {
  color: #0e76a8;
}
.social-icons {
  font-size: 1.5rem;
  margin: .5rem;
}


.heart {
  color: #E1306C;
  font-size: 1rem;
  margin-right: 5px;
  vertical-align: center;
  cursor: pointer;
}

.counters button{
  border: none;
  padding: 0;
}

.counters {
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 14px;
}